import React from 'react'
import { PieChart, Pie, Cell, Tooltip, Legend } from 'recharts';

export default function PiechartData() {

    const COLORS = ["#8884d8", "#82ca9d", "grey", "#black", "#AF19FF"];
    const COLORS1 = ["pink", "purple", "grey", "skyblue", "#AF19FF"];

    const data = [
        { name: 'Geeksforgeeks', students: 400 },
        { name: 'Technical scripter', students: 600 },
        { name: 'Geek-i-knack', students: 300 },
        { name: 'Geek-o-mania', students: 500 },
    ];
    const data1 = [
        { name: 'Geeksforgeek', students: 100 },
        { name: 'Technical script', students: 600 },
        { name: 'Geek-knack', students: 375 },
        { name: 'Geek-mania', students: 400 },
    ];

    return (
        <div style={{ display: "flex" }}>
            <h5>Client</h5>
            <PieChart width={400} height={400}>
                <Pie data={data} dataKey="students" nameKey="name" color='#000000' outerRadius={150} fill="#8884d8" >
                    {data.map((entry, index) => (
                        <Cell
                            key={`cell-${index}`}
                            fill={COLORS[index % COLORS.length]}
                        />
                    ))}
                    <Tooltip >
                        {/* <label>{`${payload[0].name} : ${payload[0].value}%`}> </label> */}
                    </Tooltip>
                    <Legend />
                </Pie>
            </PieChart>

            {/* https://www.tutorialspoint.com/reactjs-how-to-create-a-pie-chart-using-recharts */}

            <h5>Salary</h5>
            <PieChart width={400} height={400}>
                <Pie data={data1} dataKey="students" nameKey="name" color='#000000' outerRadius={150} fill="#8884d8" >
                    {data1.map((entry, index) => (
                        <Cell
                            key={`cell-${index}`}
                            fill={COLORS1[index % COLORS1.length]}
                        />
                    ))}
                </Pie>
            </PieChart>
        </div>
    )
}
