import React, { useState, useEffect } from 'react';
import axios from 'axios';
import "../API/api"
import { useNavigate } from 'react-router-dom';

const initialDataClient = {
  clientName: "",
  location: ""
}

const ClientForm = () => {
  const navigate = useNavigate();
  const [datalist, setDatalist] = useState([]);
  const heading = ['Client Name', 'Location', "Status", 'Edit', 'Delete'];
  const [data, setData] = React.useState(initialDataClient);
  const { clientName, location } = data;


  const GetClientDetails = () => {
    axios.get(`${global.API_URL}/smg/client/getAllClientDetails`).then((res) => {
      console.log("Fetched", res.data);
      setDatalist(res.data)
    }).catch((err) => {
      console.log("some error", err);
    })
  }

  useEffect(() => {
    GetClientDetails();

  }, [])

  const onDeleteClient = (clientId) => {
    axios.post(`${global.API_URL}/smg/client/deleteClientDetails/${clientId}`)
      .then(() => {
        window.location.reload(false);
        GetClientDetails();
      })
  }

  const onEditClient = (clientId, clientName, location, status) => {
    localStorage.setItem("CLIENT_Id", clientId);
    localStorage.setItem("CLIENT_NAME", clientName);
    localStorage.setItem("LOCATION", location);
    localStorage.setItem("STATUS", status);
    navigate('/profilepage/updateclient');
  }


  const inputHandle = (e) => {
    setData({
      ...data,
      [e.target.name]: [e.target.value]
    })
  }



  const handleSubmitClient = (e) => {
    e.preventDefault();
    axios.post(`${global.API_URL}/smg/client/addClient`, {
      clientName: `${clientName}`,
      location: `${location}`,
      status: true
    }).then((res) => {
      console.log("successfully posted client details", res.data);

      if (res.data == "Client added successfully!!") {
        alert("client details added successfully!")
        window.location.reload(false);

        navigate("/profilepage/clientform")
      }
    }).catch((err) => {
      console.log("some error here", err);
    })
  }

  return (
    <>
      <form onSubmit={handleSubmitClient}>
        <h5>Add Client</h5>
        <div className='ProjectField'>
          <div>
            <label className='form-label'>Client Name <sup className='star'>*</sup></label>
            <input type="text" value={clientName} name="clientName" onChange={inputHandle} className='form-control ' placeholder='Enter client name' />
          </div>
          <div>
            <label className='form-label'>Client Location <sup className='star'>*</sup></label>
            <input type="text" value={location} name="location" onChange={inputHandle} className='form-control' placeholder='Enter client location' />
          </div>
          <button type='submit' className='btn btn-success  Btn'>Add Client</button>
          <button type='submit' className='btn btn-warning  Btn' >Reset</button>
        </div>
      </form>
      <hr />
      <h5 style={{ marginTop: "20px" }}>Client Details</h5>

      <div className='Bord'>
        <table className="table table-striped">
          <thead>
            <tr>
              {
                heading.map((data, index) => (

                  <td key={index} style={{ fontWeight: '600', color: '#252F40' }}>{data}</td>

                ))
              }
            </tr>

          </thead>
          <tbody className='TBody'>
            {
              datalist.map((items) => {
                return <tr key={items.clientId}>
                  <td>{items.clientName}</td>
                  <td>{items.location}</td>
                  <>{items.status ? <td style={{ color: "green" }}> Active </td> : <td style={{ color: "red" }}>Inactive</td>}</>
                  {/* <td>{items.status ? "Active" : "Inactive"}</td> */}
                  <td className='bi bi-pen-fill' onClick={() => onEditClient(items.clientId, items.clientName, items.location, items.status)}></td>
                  <td className='bi bi-trash3-fill' style={{ color: 'red' }} onClick={() => onDeleteClient(items.clientId)}></td>
                </tr>
              })
            }
          </tbody>
        </table>
      </div>
    </>
  );
}

export default ClientForm;
