
import React, { useState, Link } from 'react';
import axios from 'axios';
import './Login.css';
import { useNavigate } from 'react-router-dom';
import Dialog from "@mui/material/Dialog";
import CloseIcon from "@mui/icons-material/Close";
import { IconButton } from "@mui/material";
import "../API/api"

const Login = () => {

  const [successLogin, setSuccesslogin] = useState(false);
  const [err, setErr] = useState("");
  const [user, setUser] = useState("");
  const [values, setValues] = useState({
    email: "",
    password: ""
  })

  const navigate = useNavigate();
  const handleChange = (e) => {
    setErr("");
    setUser("");
    setValues({
      ...values,
      [e.target.name]: [e.target.value]
    })
  }

  const { email, password } = values;

  const handleSubmit = (e) => {
    e.preventDefault();
    axios.post(`${global.API_URL}/smg/admin/adminLogin`, {
      email: `${email}`,
      password: `${password}`,
    })
      .then((res) => {
        console.log("api working", res.data)
        let adminId = res.data.adminId;
        window.localStorage.setItem("ADMIN_ID", JSON.stringify(adminId));

        if (res.data.msg == "login Successfil") {
          setTimeout(() => {
            navigate("/profilepage/employeelist")
          }, 1000);
          setSuccesslogin(true);
        }
        else if (res.data.msg == "aAdmin not present") {
          setUser("User does not exist")
        }
        else if (res.data.msg == "please Enter corrrect Username & Password") {
          setErr("Password incorrect")
        }

      }).catch((error) => {
        console.log('some error', error)
      })
  }

  const handleSuccessClose = () => {
    setSuccesslogin(false)
  }

  return (
    <>
      <Dialog open={successLogin} onClose={handleSuccessClose} >
        <form style={{ padding: "25px", zIndex: "1", width: '30vw', }}>
          <div style={{ float: 'right' }}>
            <IconButton onClick={handleSuccessClose} aria-label="close" >
              <CloseIcon style={{}} />
            </IconButton>
          </div>
          <h5 style={{ textAlign: 'center' }}>Login Successful</h5>
        </form>
      </Dialog>

      <div className='Bg-Image'>
        <div className='ImgDiv'>
          <img src='https://smartgig.s3.ap-south-1.amazonaws.com/white.png' alt='logo' className='Logo' />
        </div>
        <div className='LoginContent'>
          <div className='LoginForm'>
            <img src='https://smartgig.s3.ap-south-1.amazonaws.com/1.png' alt='symbol' width="100" height="100" />
            <h4>Sign In to your Account</h4>
            <span style={{ color: 'gray' }}>Welcome Back! Please Enter Your Details</span>

            <form className='formInput' onSubmit={handleSubmit}>
              <div>
                <input type="email" name="email" className='form-control' placeholder='Email' value={email} onChange={handleChange} />
                <p style={{ color: "red", textAlign: "left", fontSize: "smaller" }}>{user}</p>
              </div>
              <div>
                <input value={password} name="password" onChange={handleChange} className='form-control' type="password" placeholder='Password'></input>
                <p style={{ color: "red", textAlign: "left", fontSize: "smaller" }}>{err}</p>
              </div>
              <div className='d-grid'>
                <button className='btn' type="submit" style={{ backgroundColor: '#031e30', color: 'white' }}>Login</button>
              </div>
            </form>
          </div>


        </div >
      </div >
    </>
  );
}

export default Login