import React, { useState, useEffect } from 'react';
import axios from 'axios';
import "../API/api"


const MultipleFileInput = () => {
    const [employeeimage, setEmployeeimage] = useState('');
    const [certificate10th, setCertificate10th] = useState('');
    const [certificate12th, setCertificate12th] = useState('');
    const [graduation, setGraduation] = useState('');
    const [postGraduation, setPostGraduation] = useState('');
    const[offerLetter1, setOfferLetter1]= useState('');
    const[paySlip, setPaySlip] = useState('');

    function onFileUpload(e) {
        e.preventDefault()
        console.log(e.target.files);
        setEmployeeimage(e.target.files[0]);
        setCertificate10th(e.target.files[0]);
        setCertificate12th(e.target.files[0]);
        setGraduation(e.target.files[0]);
        setPostGraduation(e.target.files[0]);
        setOfferLetter1(e.target.files[0]);
        setPaySlip(e.target.files[0]);
    }
    // handle submit button for form
    function handleSubmit(e) {
        const id = window.localStorage.getItem('EDIT_ID')
        e.preventDefault();
        const formData = new FormData();
        formData.append('image', employeeimage);
        formData.append('ssccertificate', certificate10th)
        formData.append('hsccertificate', certificate12th)
        formData.append('graduation', graduation)
        formData.append('postgraduation', postGraduation)
        formData.append('offerletter', offerLetter1)
        formData.append('payslip', paySlip)
        axios.post(`${global.API_URL}/smg/employee/uploadoc/4`, formData)
            .then((res) => {
                console.log("docs successfull uploaded", res.data);
            })
            .catch((error) => {
                console.log("some error during docs uplaod", error);
            })
        
    }

    return (
        <form onSubmit={handleSubmit} className="upload--container">
            <div className='mainWrapper'>
                <div className='inneroneD'>
                    <div>
                        <label className='form-label'> Upload Photo <sup className='star'>*</sup></label>
                        <input type="file" multiple name="employeeimage" onChange={onFileUpload} accept=".jpeg, .pdf" className='form-control w-75' placeholder='Enter Client name' />
                    </div>
                    <div>
                        <label className='form-label'> Xth Marksheet <sup className='star'>*</sup></label>
                        <input type="file" name="certificate10th" onChange={onFileUpload} accept=".jpeg, .pdf" className='form-control w-75' placeholder='Enter Client name' />
                    </div>
                    <div>
                        <label className='form-label'>XIIth/Diploma Marksheet <sup className='star'>*</sup></label>
                        <input type="file"  name="certificate12th" onChange={onFileUpload} accept=".jpeg, .pdf" className='form-control w-75' placeholder='Enter Project name' />
                    </div>

                </div>
                <div className='inneroneD'>
                    <div>
                        <label className='form-label'> UG Marksheet <sup className='star'>*</sup></label>
                        <input type="file"  name="graduation" onChange={onFileUpload} accept=".jpeg, .pdf" className='form-control w-75' placeholder='Enter employee name' />
                    </div>
                    <div>
                        <label className='form-label'>PG Marksheet</label>
                        <input type="file"  name="postGraduation" onChange={onFileUpload} accept=".jpeg, .pdf" className='form-control w-75' placeholder='Enter employee name' />

                    </div>
                </div>
                <div className='inneroneD'>
                    <div>
                        <label className='form-label'>Company Offer Letter 1 </label>
                        <input type="file"  name="offerLetter1" onChange={onFileUpload} accept=".jpeg, .pdf" className='input-group form-control w-75' />
                    </div>
                    <div>
                        <label className='form-label'>Company Offer Letter 2 </label>
                        <input type="file"  name="offerLetter2" onChange={onFileUpload} accept=".jpeg, .pdf" className='input-group form-control w-75' />
                    </div>
                    <div>
                        <label className='form-label'>Company Offer Letter 3</label>
                        <input type="file" name="offerLetter3" onChange={onFileUpload} accept=".jpeg, .pdf" className='input-group form-control w-75' />
                    </div>
                    <div>
                        <label className='form-label'>Company Payslip</label>
                        <input type="file"  name="paySlip" onChange={onFileUpload} accept=".jpeg, .pdf" className='input-group form-control w-75' />

                    </div>
                </div>
            </div>
            <div className='Formbtngroup'>
                <button className='btn' type="submit" style={{ backgroundColor: '#225779', color: 'white', }} >Submit</button>
                <button className='btn fw-bold' style={{ backgroundColor: 'white', color: 'black', borderColor: 'red' }}>cancel</button>
            </div>
        </form>
    );
};

export default MultipleFileInput;