import React from 'react';
import './Profile.css'


const AssignProject = () => {
  return (
    <div >
      <form>
        <h5>Assign Project</h5>
        <div className='formWrapper'>
         <div>
          <lable className='form-label'>Employee Name <sup className='star'>*</sup></lable>
          <select className='form-select'>
            <option>Select</option>
          </select>
         </div>
         <div>
          <lable className='form-label'>Project Name</lable>
          <select className='form-select'>
            <option>Select</option>
          </select>
         </div>
         <div>
          <lable className='form-label'>Employee Name</lable>
          <input type='date' className='form-control' />
         </div>
         <button type='submit' className='btn btn-success Btn'>Assign Project</button>
          <button type='submit' className='btn btn-warning Btn'>Reset</button>
        </div>
      </form>
      <hr />
    </div>
  );
}

export default AssignProject;
