import React, { useState, useEffect } from 'react';
import './Profile.css';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import "../API/api"

const initialData = {
    employeeDetailsId: null,
    fullName: "",
    empId: "",
    gender: "",
    dateOfJoining: "",
    primaryLocation: "",
    currentLocation: "",
    qualification: "",
    primarySkills: "",
    secondrySkills: "",
    dateOfBirth: "",
    dateOfMarriage: "",
    ctc: 0,
    degination: "",
    empType: "",
    status: true,
    lastWorkingDay: "",
    email: "",
    mobileNo: 0
}


export default function EditProfile() {

    const [data, setData] = useState(initialData);
    const navigate = useNavigate();
    const {
        employeeDetailsId,
        fullName,
        empId,
        gender,
        dateOfJoining,
        primaryLocation,
        currentLocation,
        qualification,
        primarySkills,
        secondrySkills,
        dateOfBirth,
        dateOfMarriage,
        ctc,
        degination,
        empType,
        lastWorkingDay,
        email,
        mobileNo } = data


    useEffect(() => {
        setData(
            {
                employeeDetailsId: window.localStorage.getItem("EDIT_ID"),
                ctc: window.localStorage.getItem("CTC"),
                currentLocation: window.localStorage.getItem("CRNTLOCATION"),
                degination: window.localStorage.getItem("DEGINATION"),
                dateOfBirth: window.localStorage.getItem("DOB"),
                dateOfJoining: window.localStorage.getItem("DOJ"),
                dateOfMarriage: window.localStorage.getItem("DOM"),
                email: window.localStorage.getItem("EMAIL"),
                empId: window.localStorage.getItem("EMP_ID"),
                empType: window.localStorage.getItem("EMP_TYPE"),
                fullName: localStorage.getItem("FULLNAME"),
                gender: window.localStorage.getItem("GENDER"),
                mobileNo: window.localStorage.getItem("MOB_NO"),
                primaryLocation: window.localStorage.getItem("P_LOC"),
                primarySkills: window.localStorage.getItem("P_SKILLS"),
                qualification: window.localStorage.getItem("QUALIFICATION"),
                secondrySkills: window.localStorage.getItem("S_SKILLS"),
            }
        )
    }, [])

    const inputHandle = (e) => {
        setData({
            ...data,
            [e.target.name]: [e.target.value]
        })
    }


    const handleSubmitEditForm = (e) => {
        e.preventDefault();
        axios.put(`${global.API_URL}/smg/employee/editEmployeeDetails/${window.localStorage.getItem("EDIT_ID")}`, {
            // employeeDetailsId: `${employeeDetailsId}`,
            fullName: `${fullName}`,
            empId: `${empId}`,
            gender: `${gender}`,
            dateOfJoining: `${dateOfJoining}`,
            primaryLocation: `${primaryLocation}`,
            currentLocation: `${currentLocation}`,
            qualification: `${qualification}`,
            primarySkills: `${primarySkills}`,
            secondrySkills: `${secondrySkills}`,
            dateOfBirth: `${dateOfBirth}`,
            dateOfMarriage: `${dateOfMarriage}`,
            ctc: `${ctc}`,
            degination: `${degination}`,
            empType: `${empType}`,
            status: true,
            lastWorkingDay: `${lastWorkingDay}`,
            email: `${email}`,
            mobileNo: `${mobileNo}`
        })
            .then((res) => {
                console.log("data updated", res.data);
                alert("Data updated successfully");
                navigate('/profilepage/employeelist')

            }).catch((error) => {
                console.log("Error found", error)
            })
    }



    return (
        <div>
            <form onSubmit={handleSubmitEditForm}>
                <div className='mainWrapper'>
                    <div className='innerone'>
                        <div>
                            <label className='form-label'> Full Name <sup className='star'>*</sup></label>
                            <input type="text" onChange={inputHandle} value={fullName} name="fullName" className="form-control w-75" id="pwd" placeholder='Enter Your Full Name' />

                        </div>

                        <div className='DateRow'>
                            <div className='inneronerow'>
                                <label className='form-label'>Gender <sup className='star'>*</sup></label>
                                <select className='gnder form-select w-75' onChange={inputHandle} value={gender} name="gender" >
                                    <option>
                                        Select
                                    </option>
                                    <option>
                                        Male
                                    </option>
                                    <option>
                                        Female
                                    </option>
                                </select>
                            </div>
                            <div className='inneronerow'>
                                <label className='form-label'>DOB <sup className='star'>*</sup></label>
                                <input type="date" onChange={inputHandle} value={dateOfBirth} name="dateOfBirth" className="form-control w-75" id="pwd" />
                            </div>
                        </div>
                        <div>
                            <label className='form-label'>Mobile No <sup className='star'>*</sup></label>
                            <input type="number" onChange={inputHandle} value={mobileNo} name="mobileNo" className="form-control w-75" placeholder='Enter Your Number' />
                        </div>
                        <div>
                            <label className='form-label'>Employee Id <sup className='star'>*</sup></label>
                            <input type="text" onChange={inputHandle} value={empId} name="empId" className="form-control w-75" id="pwd" placeholder='Enter Your Employee Id' />
                        </div>
                        <div>
                            <label className='form-label'> Email Id <sup className='star'>*</sup></label>
                            <input type="email" onChange={inputHandle} value={email} name="email" className="form-control w-75" id="pwd" placeholder='Enter Email  Id' />

                        </div>
                    </div>
                    <div className='innerone'>

                        <div>
                            <label className='form-label'>Employee Type <sup className='star'>*</sup></label>
                            <select className='detailsType form-select w-75' onChange={inputHandle} value={empType} name="empType">
                                <option>Select Employee Type</option>
                                <option>Intern</option>
                                <option>Full Time</option>
                                <option>Part Time</option>
                            </select>
                        </div>
                        <div>
                            <label className='form-label'>Designation <sup className='star'>*</sup></label>
                            <input type="text" onChange={inputHandle} value={degination} name="degination" className="form-control w-75" id="pwd" placeholder='Enter Your Designation' />
                        </div>
                        <div>
                            <label className='form-label'>Qualification <sup className='star'>*</sup></label>
                            <input type="text" onChange={inputHandle} value={qualification} name="qualification" className="form-control w-75" id="pwd" placeholder='Enter Your Qualifications' />
                        </div>
                        <div>
                            <label className='form-label'>Primary Location <sup className='star'>*</sup></label>
                            <input type="text" onChange={inputHandle} value={primaryLocation} name="primaryLocation" className="form-control w-75" id="pwd" placeholder='Enter Your Primary Location' />
                        </div>
                        <div>
                            <label className='form-label'>Current Location<sup className='star'>*</sup></label>
                            <input type="text" onChange={inputHandle} value={currentLocation} name="currentLocation" className="form-control w-75" id="pwd" placeholder='Enter Your Current Location' />

                        </div>
                    </div>
                    <div className='innerone'>

                        <div>
                            <label className='form-label'>Primary Skills<sup className='star'>*</sup></label>
                            <input type="text" onChange={inputHandle} value={primarySkills} name="primarySkills" className="form-control w-75" id="pwd" placeholder='Enter Your Primary skills' />

                        </div>
                        <div>
                            <label className='form-label'>Secondary Skills <sup className='star'>*</sup></label>
                            <input type="text" onChange={inputHandle} value={secondrySkills} name="secondrySkills" className="form-control w-75" id="pwd" placeholder='Enter Your Secondary Skills' />
                        </div>
                        <div className='DateRow'>
                            <div className='inneronerow'>
                                <label className='form-label'>DOM <sup className='star'>*</sup></label>
                                <input type="date" onChange={inputHandle} value={dateOfMarriage} name="dateOfMarriage" className="form-control w-75" id="pwd" />

                            </div>
                            <div className='inneronerow'>
                                <label className='form-label'>DOJ <sup className='star'>*</sup></label>
                                <input type="date" className="form-control w-75" id="pwd" name="dateOfJoining" onChange={inputHandle} value={dateOfJoining} />

                            </div>
                        </div>
                        <div>
                            <label className='form-label'>Last Working Day <sup className='star'>*</sup></label>
                            <input type="date" onChange={inputHandle} value={lastWorkingDay} name="lastWorkingDay" className="form-control w-75" id="pwd" placeholder='Enter Your Employee Id' />
                        </div>
                        <div>
                            <label className='form-label'>CTC <sup className='star'>*</sup></label>
                            <input type="text" onChange={inputHandle} value={ctc} name="ctc" className="form-control w-75" id="pwd" placeholder='Enter Your CTC' />
                        </div>
                    </div>
                </div>
                <div className='Formbtngroup'>
                    <button className='btn' type="submit" style={{ backgroundColor: '#225779', color: 'white', }} >Next</button>
                    <button className='btn fw-bold' style={{ backgroundColor: 'white', color: 'black', borderColor: 'red' }}>cancel</button>
                </div>
            </form>
        </div>
    )
}
