import React from 'react';
import './Profile.css';


const Billing = () => {
    return (
        <div>
            <form>
                <h5>Billing</h5>
                <div className='mainWrapper'>
                    <div className='innerone'>
                        <div>
                            <label className='form-label'> Employee Name <sup className='star'>*</sup></label>
                           <select className='form-select w-75'>
                            <option>select</option>
                           </select>
                        </div>
                        <div className='DateRow'>
                            <div className='inneronerow'>
                                <label className='form-label'>From Date <sup className='star'>*</sup></label>
                                <input type="date" className="form-control w-75"  />

                            </div>
                            <div className='inneronerow'>
                                <label className='form-label'>To Date <sup className='star'>*</sup></label>
                                <input type="date" className="form-control w-75"  />

                            </div>
                        </div>
                        <div>
                            <label className='form-label'>Total Days<sup className='star'>*</sup></label>
                            <input type='text' className='form-control w-75' />
                        </div>
                    </div>
                    <div className='innerone'>
                    <div>
                            <label className='form-label'> Client Name <sup className='star'>*</sup></label>
                           <select className='form-select w-75'>
                            <option>select</option>
                           </select>
                        </div>
                        <div>
                            <label className='form-label'>Day<sup className='star'>*</sup></label>
                            <input type='text' className='form-control w-75' />
                        </div>
                        <div>
                            <label className='form-label'>Day/Rate<sup className='star'>*</sup></label>
                            <input type='text' className='form-control w-75' />
                        </div>
                    </div>
                    <div className='innerone'>
                    <label className='form-label'> Project Name <sup className='star'>*</sup></label>
                           <select className='form-select w-75'>
                            <option>select</option>
                           </select>
                           <div>
                            <label className='form-label'>Total no. of Leaves<sup className='star'>*</sup></label>
                            <input type='text' className='form-control w-75' />
                        </div>
                        <div>
                            <label className='form-label'>Total Amount<sup className='star'>*</sup></label>
                            <input type='text' className='form-control w-75' />
                        </div>
                    </div>
                </div>

                <div className='btngroupBilling'>
                    <button className='btn' type="submit" style={{ backgroundColor: '#225779', color: 'white', }}>Submit</button>
                    <button className='btn fw-bold' type="submit" style={{ backgroundColor: 'white', color: 'black', borderColor: 'red' }}>cancel</button>
                </div>
            </form>
        </div>
    );
}

export default Billing;
