import axios from 'axios';
import React, { useEffect, useRef } from 'react';
import './Profile.css'
import { useNavigate } from 'react-router-dom';
import "../API/api"

const initialdataProject = {
  projectName: "",
  projectDescription: "",
  status: "",
  client: ''
}

const ProjectForm = () => {
  const refElement = useRef("");
  const [optionList, setOptionList] = React.useState([]);
  const navigate = useNavigate();
  const [values, setValues] = React.useState([]);
  const [data, setData] = React.useState(initialdataProject);

  const heading = ['Project Name', 'Project Description', "ClientName", "Status", 'Edit']

  const handleReset = () => {
    refElement.current.focus()
  }

  const {
    projectName,
    projectDescription,
    status,
    client
  } = data;

  const inputHandle = (e) => {

    setData({
      ...data,
      [e.target.name]: [e.target.value]
    })
  }

  const handleProjects = (e) => {
    e.preventDefault();
    axios.post(`${global.API_URL}/smg/project/saveProjectDetails`, {
      projectName: `${projectName}`,
      projectDescription: `${projectDescription}`,
      clientName: `${client}`,
      status: `${status}`
    })
      .then((res) => {
        console.log("details saved into the database", res.data);
        if (res.data.message == "details was saved into the database.......") {
          navigate("/profilepage/clientform")
          alert("Project details saved successfully")
        }

      })
      .catch((err) => {
        console.log("error found", err);
      })
  }

  const GetProjectDetails = () => {
    axios.get(`${global.API_URL}/smg/project/getAllProjectDetails`).then((res) => {
      console.log("Fetch successfully", res.data);
      setValues(res.data)
    }).catch((err) => {
      console.log("some error", err);
    })
  }
  const GetClientDetails = () => {
    axios.get(`${global.API_URL}/smg/client/getAllClientDetails`).then((res) => {
      console.log("ListData fetched", res.data);
      setOptionList(res.data)
    }).catch((err) => {
      console.log("some error", err);
    })
  }

  useEffect(() => {
    GetProjectDetails();
    GetClientDetails();
  }, [])

  const editProject = (projectId, projectName, projectDes, projectStatus, clientId) => {
    localStorage.setItem("Id", projectId);
    localStorage.setItem("P_Name", projectName);
    localStorage.setItem("P_Des", projectDes);
    localStorage.setItem("Status", projectStatus);
    localStorage.setItem("Client_ID", clientId)
    navigate('/profilepage/updateproject');
  }


  return (
    <>
      <div>
        <form onSubmit={handleProjects}>
          <h5>Add Project</h5>
          <div className='ProjectField'>
            <div>
              <label className='form-label'>Project Name <sup className='star'>*</sup></label>
              <input type="text" ref={refElement} value={projectName} name="projectName" onChange={inputHandle} className='form-control ' placeholder='Enter Project name' />
            </div>
            <div>
              <label className='form-label'>Project Details <sup className='star'>*</sup></label>
              <input type="text" ref={refElement} value={projectDescription} name="projectDescription" onChange={inputHandle} className='form-control' placeholder='Enter Project details' />
            </div>
            <div className='SOptions'>
              <label className='form-label'>Project Status <sup className='star'>*</sup></label>
              <select value={status} name="status" className='form-select ' onChange={inputHandle}>
                <option>select</option>
                <option>
                  InProgress
                </option>
                <option>
                  Completed
                </option>
                <option>
                  Pending
                </option>
              </select>
            </div>
            <div className='SOptions'>
              <label>Select Client</label>
              <select className='form-select mt-2' value={client} name="client" onChange={inputHandle}>
                <option>Select</option>
                {
                  optionList.map((list) => {
                    return <option key={list.clientId} >{list.clientName}</option>
                  })
                }
              </select>
            </div>
            <button type='submit' className='btn btn-success  Btn'>Add Project</button>
            <button type='submit' onClick={handleReset} className='btn btn-warning  Btn'>Reset</button>
          </div>
        </form >
      </div >
      <hr />
      <h5 style={{ marginTop: "20px" }}>Project Details</h5>
      <div className='Bord'>
        <table className="table table-striped">
          <thead>
            <tr>
              {
                heading.map((data, index) => (

                  <td key={index} style={{ fontWeight: '600', color: '#252F40' }}>{data}</td>

                ))
              }
            </tr>

          </thead>
          <tbody className='TBody'>
            {
              values.map((value, index) => {
                return <tr key={index}  >
                  <td style={{ color: '#2562AB', cursor: 'pointer' }} >{value.projectName}</td>
                  <td >{value.projectDes}</td>
                  <td >{value.ClientName}</td>
                  <td >{value.projectStatus}</td>
                  <td className='bi bi-pen-fill' onClick={() => editProject(value.projectId, value.projectName, value.projectDes, value.projectStatus, value.clientId)}></td>
                </tr>
              })
            }
          </tbody>
        </table>
      </div>
    </>
  );
}

export default ProjectForm;
