import axios from 'axios';
import React, { useEffect, useState } from 'react';
import './Profile.css'
import { useNavigate } from 'react-router-dom';
import "../API/api"


const UpdateProject = (props) => {
  const [clientName, setClientName] = React.useState('');
  const [optionList, setOptionList] = React.useState([]);
  const [id, setId] = useState(null)
  const [projectName, setprojectName] = useState('');
  const [Pdetails, setPdetails] = useState('')
  const [status, setprojectStatus] = useState('')
  // const[client, setClient] = useState('') 
  // const[clientId, setClientId]= useState('')
  const navigate = useNavigate();


  const handleUpdate = (e) => {
    e.preventDefault();
    axios.put(`${global.API_URL}/smg/project/editProjectDetails/${id}`, {
      projectName: projectName,
      projectDescription: Pdetails,
      clientName: clientName,
      status: status
    })
      .then((res) => {
        console.log("successfully project details updated", res.data);
        if (res.data.message == "details is updated successfully") {
          navigate("/profilepage/clientform")
          alert("project details updated successfully")
        }

      })
      .catch((err) => {
        console.log("error found", err);
      })
  }

  useEffect(() => {
    setId(localStorage.getItem('Id'));
    setprojectName(localStorage.getItem('P_Name'));
    setPdetails(localStorage.getItem('P_Des'));
    setprojectStatus(localStorage.getItem('Status'));
    setClientName(localStorage.getItem('Client_Name'));

    GetClientDetails();
  }, [])


  const GetClientDetails = () => {
    axios.get(`${global.API_URL}/smg/client/getAllClientDetails`).then((res) => {
      console.log("ListData fetched", res.data);
      setOptionList(res.data)
    }).catch((err) => {
      console.log("some error", err);
    })
  }

  return (
    <>
      <div>
        <form >
          <h5>Add Project</h5>
          <div className='ProjectField'>
            <div>
              <label className='form-label'>Project Name <sup className='star'>*</sup></label>
              <input type="text" value={projectName} name="projectName" onChange={(e) => setprojectName(e.target.value)} className='form-control ' placeholder='Enter Project name' />
            </div>
            <div>
              <label className='form-label'>Project Details <sup className='star'>*</sup></label>
              <input type="text" value={Pdetails} name="Pdetails" onChange={(e) => setPdetails(e.target.value)} className='form-control' placeholder='Enter Project details' />
            </div>
            <div className='SOptions'>
              <label className='form-label'>Project Status <sup className='star'>*</sup></label>
              <select value={status} name="status" className='form-select ' onChange={(e) => setprojectStatus(e.target.value)}>
                <option>select</option>
                <option>
                  OnProgress
                </option>
                <option>
                  Completed
                </option>
                <option>
                  Pending
                </option>
              </select>
            </div>
            <div className='SOptions'>
              <label>Select Client</label>
              <select className='form-select mt-2' value={clientName} onChange={(e) => setClientName(e.target.value)}>
                <option>select</option>
                {
                  optionList.map((list) => {
                    return <option key={list.clientId} value={list.clientName}>{list.clientName}</option>
                  })
                }
              </select>
            </div>
            <button type='submit' className='btn btn-success  Btn' onClick={handleUpdate}>Update Project</button>
          </div>
        </form >
      </div >
    </>
  );
}

export default UpdateProject;